.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
  max-width: 375px;
    overflow-y: auto;
}

#root {
  width: 375px;
  margin-bottom: 100px;
}

.content p{
  max-width: 375px;
}

.dice {
  display: inline-block;
  min-width: 60px;
  min-height: 60px;
  background: #f5f5f5;
  color: #222;
  padding: 10px;
  font-size:  24px;
  text-align: center;
  margin-right: 5px;
  margin-bottom: 10px;
  -webkit-transition: linear 0.4s ;
  -moz-transition: linear 0.4s ;
  -o-transition: linear 0.4s ;
  transition: linear 0.4s;
  border: none;
}

#status {
  clear: left;
}

.dice.rolled {
  transform:rotate(360deg);
  -ms-transform:rotate(360deg);
  -webkit-transform:rotate(360deg);
  background: #BBBBBB;
}

#red {
  color: #FFF;
  background-color: red;
}

#blue {
  color: #FFF;
  background-color: blue;
}

#green {
  color: #FFF;
  background-color: green;
}

#yellow {
  color: #FFF;
  background-color: gold;
}

#purple {
  color: #FFF;
  background-color: purple;
}

#roll-button {
  display: block;
  width: 90px;
  text-decoration: none;
  margin: 10px 25px;
  padding: 10px 30px;
  background: black;
  color: white;
  text-align: center;
  border: 1px solid black;
}

#roll-button:hover {
  background: #DDD;
  color: black;
  border: 1px solid #DDD;
}

#reset-button {
  display: block;
  width: 90px;
  text-decoration: none;
  margin: 10px 5px;
  padding: 10px 30px;
  background: white;
  color: black;
  text-align: center;
  border: 1px solid black;
}

#score-button {
  display: block;
  width: 100px;
  text-decoration: none;
  margin: 0 10px 10px 25px;;
  padding: 10px 30px;
  background: white;
  color: black;
  text-align: center;
  border: 1px solid black;
}

#reset-button:hover {
  color: black;
  background-color: #DDD;
  border: 1px solid #DDD;
}

#score-button {
  width: 150px;
  border: 1px solid #0575FF;
  color: #0575FF;
  background: #FFF;
}
#score-button:hover {
  color: white;
  background-color: #0575FF;
}

#dice-buttons {
  margin: 20px 0 0 25px;
}

.toggled {
  background-color: #999 !important;
  color: #FFF !important;
  border: none;
}

.which-roll {
  margin: 5px 0 10px 0;
  font-style: italic;
}
.indent-2 {
  margin-left: 5px;
  margin-top: 10px;
  display: block;
}
.center {
  text-align: center;
}
h3, h4 {
  margin: 10px;
}
#score-pad-form {
  margin-left: 25px;
}
#score-pad-form p{
  margin: 0px;
}
.display-single-score {
  color: #0575FF;
  padding-left: 12px;
  font-weight: 700;
}
.display-single-score-total {
  color: purple;
}
#instructions {
  padding: 0 25px;
}

#reset-game, .collapse-button, #score-button {
  margin-left: 25px;
  background: white;
  color: #0575FF;
  display: block;
  width: 320px;
  margin: 0 0 10px 25px;
  padding: 5px 0;
  font-size: 16px;
  border: #0575FF 1px solid;
}

#reset-game:hover, .collapse-button:hover {
  background-color: #0575FF;
  color: white;
}
.collapse-button {
  margin-top: 25px;
}
/* Style the button that is used to open and close the collapsible content */
.collapsible {
  background-color: #eee;
  color: #444;
  cursor: pointer;
  padding: 18px;
  width: 100%;
  border: none;
  text-align: left;
  outline: none;
  font-size: 15px;
}

.collapse-content.collapsed {
  display: none;
}

.collapsed-content.expanded {
  display: block;
}

.fiftygrid {
  display: grid;
  grid-template-columns: 50% 50%;
}
#left-half > p {
  margin-top: 0px;
}
